import React from 'react'

export default function Social({children, link="#", tooltip=link}) {

  return (
    <a href={link} title={tooltip} target='_blank' download className='py-6 px-9 max-md:py-4 max-md:px-7 m-1 rounded-full bg-neutral-800 dark:bg-neutral-300 hover:text-neutral-100 hover:dark:text-neutral-900 dark:text-neutral-800 flex items-center text-lg text-neutral-50 hover:bg-neutral-900 dark:hover:bg-neutral-100  max-md:text-md max-md:w-100 fade-up duration-300'>
    {/* // <a href={link} className='py-7 px-9 m-1 rounded-3xl bg-white border-2 border-neutral-800 flex items-center text-lg text-neutral-900 hover: hover:bg-gray-800 hover:duration-500 max-md:px-8 max-md:text-md max-md:w-100 fade-up'> */}
        {children}
    </a>
  )
}
