import React from 'react'

export default function Info({ children, classlist=  "", id=null}) {
    let classes = `px-6 py-4 m-1 rounded-3xl bg-white dark:bg-neutral-800 dark:text-neutral-200 dark:hover:bg-neutral-100 dark:hover:text-neutral-800 flex items-center text-lg text-neutral-700 hover:bg-neutral-900 hover:text-neutral-50 hover:duration-500 max-md:px-4 max-md:py-3 max-md:text-sm text-md max-md:w-100 fade-up ${ classlist }`
    let last_classes = `px-3 py-3 m-4 rounded-3xl bg-red-600 flex items-center text-lg text-neutral-700 hover:bg-neutral-800 hover:text-neutral-50 hover:duration-500 max-md:px-6 max-md:py-8 max-md:text-md max-md:w-100 fade-up flex-none ${ classlist }`
    if (children===" "){
        return (
            <div >
                <div className={last_classes}> </div>
            </div>
        )
    }
    return (
    <div >
        <div className={classes} id={id}>{ children }</div>
    </div>
  )
}
