import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub} from "@fortawesome/free-brands-svg-icons";
import { faComputer } from "@fortawesome/free-regular-svg-icons"
import { faEllipsis, faMicrochip } from "@fortawesome/free-solid-svg-icons";

export default function Skill({main_skills, other_skills, languages, libnframeworks}){

    const main_skillArray = (main_skills || "").split(", ").map((word, index) => (
        <div key={index} className="fade-up bg-white dark:bg-neutral-800 dark:text-neutral-200 dark:hover:bg-neutral-100 dark:hover:text-neutral-800 hover:bg-neutral-900 hover:text-neutral-50 duration-500 mr-1 py-2 px-4 rounded-3xl mt-2 text-sm max-md:text-xs">{word}</div>
      ));

      const inModalPillClass = "bg-neutral-200 hover:bg-neutral-800 hover:text-neutral-200 dark:bg-neutral-900 dark:text-neutral-200 dark:hover:bg-neutral-100 dark:hover:text-neutral-800 text-sm mr-1 py-2 max-md:py-1 px-3 rounded-3xl mt-2 duration-500";
    
    const all_skillArray = (main_skills + ", " + other_skills || "").split(", ").map((word, index) => (
        <div key={index} className={inModalPillClass}>{word}</div>
      ));
    
      const languagesArray = (languages || "").split(", ").map((word, index) => (
        <div key={index} className={inModalPillClass}>{word}</div>
      ));

      const libnframeworkArray = (libnframeworks || "").split(", ").map((word, index) => (
        <div key={index} className={inModalPillClass}>{word}</div>
      ));




        const [isModalOpen, setIsModalOpen] = useState(false);
      
        const handleOpenModal = () => setIsModalOpen(true);
        const handleCloseModal = () => setIsModalOpen(false);
      
        const handleOverlayClick = (e) => {
          // Close the modal only if the user clicks on the overlay, not the modal content
          if (e.target.id === "modal-overlay") {
            handleCloseModal();
          }};

      return(
        <>
        <div className='flex flex-wrap mt-4 justify-center'>
          {/* <div className="py-2 px-4 bg-neutral-800 text-neutral-200 rounded-3xl mr-2">Skills : </div> */}
            {main_skillArray}
            <button className="fade-up bg-red-600 hover:bg-red-500 text-neutral-100 text-sm max-md:text-xs mr-2 mt-2 py-2 px-4 rounded-3xl duration-500" onClick={handleOpenModal}><FontAwesomeIcon icon={faEllipsis} /></button>
        </div>


              {/* Modal */}
              {isModalOpen && (
                <div id="modal-overlay" className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50" onClick={handleOverlayClick}>
                  <div className="bg-gray-100 dark:bg-neutral-800 p-6 max-lg:p-4 rounded-3xl shadow-sm max-w-lg w-full m-3">
                 
                    <h2 className="text-2xl mb-2 mt-1 ms-2 dark:text-neutral-200 ">Skills</h2>
                    <div className="flex flex-wrap">
                      {all_skillArray}
                    </div>
                    <div className="flex-none mt-6 max-md:mt-5 ms-1 text-sm text-neutral-500">Libraries/Frameworks</div>
                      <div className="flex flex-wrap">
                        {libnframeworkArray}
                      </div>
                    <div className="flex-none mt-6 max-md:mt-5 ms-1 text-sm text-neutral-500">Languages</div>
                      <div className="flex flex-wrap">
                        {languagesArray}
                      </div>
                    
                  </div>
                </div>
              )}
        </>
      )
}