import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faEnvelope, faUser, faBriefcase} from "@fortawesome/free-solid-svg-icons";
export default function MailOptions() {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOpenModal = () => setIsModalOpen(true);
  const handleCloseModal = () => setIsModalOpen(false);

  const handleOverlayClick = (e) => {
    // Close the modal only if the user clicks on the overlay, not the modal content
    if (e.target.id === "modal-overlay") {
      handleCloseModal();
    }
  };

  return (
    <div className="relative">
      {/* Button to Open Modal */}
      <button onClick={handleOpenModal} className="py-6 px-9 max-md:py-4 max-md:px-7 m-1 rounded-full bg-neutral-800 dark:bg-neutral-300 hover:text-neutral-100 hover:dark:text-neutral-900 dark:text-neutral-800 flex items-center text-lg text-neutral-50 hover:bg-neutral-900 dark:hover:bg-neutral-100 max-md:text-md max-md:w-100 fade-up duration-300">
        <FontAwesomeIcon icon={faEnvelope} size='lg'/>
      </button>

      {/* Modal */}
      {isModalOpen && (
        <div id="modal-overlay" className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50" onClick={handleOverlayClick}>
          <div className="bg-gray-100 dark:bg-neutral-800 p-6 rounded-3xl shadow-lg max-w-sm w-full m-3">
         
            <h2 className="text-2xl mb-7 mt-1 dark:text-neutral-200 text-center">
              Choose an Email Option
            </h2>
            <div className="">
              {/* Option 1 */}
              <a href="mailto:anindyakbiswas5@gmail.com" className="block mb-2 text-neutral-800 dark:text-neutral-100 px-4 py-4 rounded-t-3xl bg-white dark:bg-neutral-900 hover:bg-neutral-800 hover:text-neutral-100 dark:hover:bg-neutral-200 dark:hover:text-neutral-800  duration-300 text-center">
                <FontAwesomeIcon icon={faUser} className=' me-2 opacity-80' />anindyakbiswas5@gmail.com
              </a>
              {/* Option 2 */}
              <a href="mailto:anindya_b@mfs.iitr.ac.in" className="block mb-0 text-neutral-800 dark:text-neutral-100 px-4 py-4 rounded-b-3xl bg-white  dark:bg-neutral-900 hover:bg-neutral-800 hover:text-neutral-100 dark:hover:bg-neutral-200 dark:hover:text-neutral-800 duration-300 text-center">
                <FontAwesomeIcon icon={faBriefcase} className=' me-2 opacity-80' />anindya_b@mfs.iitr.ac.in
              </a>

              {/* Close Button */}
            {/* <button onClick={handleCloseModal} className="block text-neutral-100 mx-auto py-3 px-7 rounded-full bg-red-500 border duration-300 text-center">
              Close
            </button> */}
            </div>
            
          </div>
        </div>
      )}
    </div>
  );
}
